<template>
  <div class="index-backg">
    <!-- 網站導引列 -->
    <header class="header-wrapper">
      <appHeader v-on:OpenSidebar="toggleSidebar" />
    </header>
    <!-- 加入購物車提示 -->
    <div v-if="toastMessage" class="toast">
      <p>{{ toastMessage }}</p>
    </div>
    <!-- 右側側欄 -->
    <div class="sidebar" v-bind:class="{ active: isSidebarOpen }">
      <!-- 側欄標題與關閉按鈕 -->
      <div class="sidebar-header">
        <span class="sidebar-title">購物車</span>
        <button class="close-btn" v-on:click="toggleSidebar">×</button>
      </div>
      <!-- 購物車內容 -->
      <div class="sidebar-content">
        <cartPage v-on:close-sidebar="() => toggleSidebar(false)" />
      </div>
    </div>
    <!-- 主要內容區塊 -->
    <section class="main-content">
      <router-view />
    </section>
    <!-- 聯絡我們 -->
    <div class="pg-content">
      <contactUs />
    </div>
    <!-- Footer區塊 -->
    <footer class="footer">
      <div class="social-media">
        <a href="https://www.facebook.com/" target="_blank"><img src="img/icon/facebook.png" alt="臉書 連結"></a>
        <a href="https://www.instagram.com/" target="_blank"><img src="img/icon/instagram.png" alt="IG 連結"></a>
        <a href="https://www.linkedin.com/" target="_blank"><img src="img/icon/linkedin.png" alt="LINKED IN 連結"></a>
      </div>
      <p>本網站僅供個人作品使用，未提供商業用途</p>
      <p>BBS &copy; 2025 COPYRIGHT</p>
    </footer>
    <!-- 回到最上方按鈕 -->
    <div v-if="showToTop" class="to-top" v-on:click="goTop">
      <font-awesome-icon :icon="['fas', 'chevron-up']" class="arrow" />
      <font-awesome-icon :icon="['fas', 'chevron-up']" class="arrow" />
      <font-awesome-icon :icon="['fas', 'chevron-up']" class="arrow" />
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onBeforeUnmount, provide } from "vue";
// 引入 vuex 的 useStore
import { useStore } from "vuex";
// 引入 子組件
import appHeader from "./components/appHeader.vue";
import cartPage from "./views/cartPage.vue";
import contactUs from "./components/contactUs.vue";
// 引入 共享資料
import { newsData } from "./newsData.js";
import { menuData } from "./menuData.js";

const store = useStore();

onMounted(() => {
  // 監聽滾動事件
  window.addEventListener("scroll", handleScroll);
  // 載入時讀取 localStorage 並更新 Vuex 資料
  const savedCart = localStorage.getItem("cart");
  if (savedCart) {
    store.commit("setCart", JSON.parse(savedCart));
  }
});

// 使用 Vuex 的 getters 獲取 toastMessage
const isSidebarOpen = ref(false);
const toastMessage = computed(() => store.getters.toastMessage);

// 控制側欄開關
const toggleSidebar = () => {
  isSidebarOpen.value = !isSidebarOpen.value;
};

// 返回頁面最上方
const showToTop = ref(false);
const goTop = () => {
  window.scrollTo({
    top: 0,
    behavior: "smooth",
  });
};

// 決定滾動多深會顯示返回最上方按鈕
const handleScroll = () => {
  showToTop.value = window.scrollY > 1000;
};

// 提供 newsData 和 menuData 給子元件
provide("newsData", ref(newsData));
provide("menuData", ref(menuData));

onBeforeUnmount(() => {
  // 移除監聽滾動事件
  window.removeEventListener("scroll", handleScroll);
});
</script>
