<template>
  <div class="news-list">
    <h3 v-if="showMore" class="news-title">最新消息</h3>
    <div v-if="showMore" class="news-board">
      <span class="notification"
        >每日出爐時間為中午12:00及下午7:00，新鮮現烤出爐，別錯過囉!
      </span>
    </div>
    <div class="content">
      <div
        v-for="(news, index) in displayedNews"
        v-bind:key="index"
        class="cards"
      >
        <div class="card">
          <!-- 替換圖片區域為單色塊占位 -->
          <router-link v-bind:to="'/news/' + news.id" class="card-route">
            <img
              v-if="news.image"
              v-bind:src="news.image"
              class="card-img"
              alt="新聞圖片"
            />
            <div class="card-body">
              <h5 class="card-title">{{ news.title }}</h5>
              <p class="text-muted">{{ news.date }}</p>
            </div>
          </router-link>
        </div>
      </div>
    </div>
    <div v-if="showMore" class="more-area">
      <router-link to="/news" class="more-btn"
        >查看更多 <img src="img/icon/right-side.png" alt="前往更多"
      /></router-link>
    </div>
  </div>
</template>

<script setup>
import { computed, defineProps, onMounted } from "vue";

// 定義 props
const props = defineProps({
  news: Array,
  limit: Number,
  title: String,
  showMore: Boolean,
});

// 計算要顯示的新聞數量
const displayedNews = computed(() => {
  return props.limit ? props.news.slice(0, props.limit) : props.news;
});

onMounted(() => {
  const notification = document.querySelector('.notification');
  const board = document.querySelector('.news-board');

  if (notification && board) {
    const distance = notification.offsetWidth + board.offsetWidth; // 總移動距離
    const speed = 150; // 每秒跑 100px
    const duration = distance / speed; // 秒數

    notification.style.setProperty('--marquee-duration', `${duration}s`);
  }
});

</script>
