<template>
  <div class="cart-container">
    <div v-if="cart.length === 0" class="cart-empty">
      <router-link v-bind:to="'/menu'" class="btn-primary">選購去!</router-link>
    </div>
    <ul class="cart-items">
      <li v-for="(item, index) in cart" v-bind:key="item.id" class="cart-item">
        <div class="item-des">
          <img v-bind:src="item.image" class="cart-img" />
          {{ item.name }}
        </div>
        <span class="item-info">
          {{ item.price }} NTD x {{ item.quantity }} =
          {{ item.price * item.quantity }} NTD
          <div class="quantity-controls">
            <button
              class="p-adj"
              v-on:click="updateQuantity(index, item.quantity - 1)"
            >
              ◀
            </button>
            <span>{{ item.quantity }}</span>
            <button
              class="p-adj"
              v-on:click="updateQuantity(index, item.quantity + 1)"
            >
              ▶
            </button>
            <button class="remove-button" v-on:click="removeFromCart(index)">
              移除
            </button>
          </div>
        </span>
      </li>
    </ul>
    <h4 v-if="cart.length > 0" class="total-price">
      總計金額 : {{ totalPrice }} NTD
    </h4>
    <div v-if="!(cart.length === 0)" class="order-area">
      <router-link
        v-bind:to="'/checkorder'"
        class="btn-primary"
        v-on:click="closeSidebar"
        >訂單結帳</router-link
      >
    </div>
  </div>
</template>

<script setup>
import { computed, readonly, defineEmits } from "vue";
import { useStore } from "vuex";

// 使用 Vuex store
const store = useStore();

// 取得購物車資料與總金額
const cart = readonly(computed(() => store.state.cart));
const totalPrice = computed(() => store.getters.totalPrice);

// 更新商品數量
const updateQuantity = (index, newQuantity) => {
  const updatedCart = [...cart.value];

  if (newQuantity <= 0) {
    updatedCart.splice(index, 1);
  } else {
    updatedCart[index].quantity = newQuantity;
  }

  store.commit("updateCart", updatedCart);
  localStorage.setItem("cart", JSON.stringify(updatedCart));
};

// 移除整個商品項目
const removeFromCart = (index) => {
  const updatedCart = [...cart.value];
  updatedCart.splice(index, 1);
  store.commit("updateCart", updatedCart);
  localStorage.setItem("cart", JSON.stringify(updatedCart));
};

// 定義 emit 事件
const emit = defineEmits(["close-sidebar"]);
// 關閉側欄
const closeSidebar = () => {
  emit("close-sidebar");
};
</script>
