<template>
  <nav class="navbar">
    <div class="navbar-container">
      <router-link
        to="/"
        class="navbar-brand boujee-text"
        v-on:click="closeMenu"
        >Bread Baking Studio</router-link
      >
      <!-- 漢堡按鈕 -->
      <button class="menu-toggle" @click="toggleMenu">☰</button>

      <!-- 選單 -->
      <ul class="navbar-nav" v-bind:class="{ active: isMenuOpen }">
        <li class="nav-item">
          <router-link to="/" class="nav-link" v-on:click="closeMenu"
            >首頁</router-link
          >
        </li>
        <li class="nav-item">
          <router-link to="/aboutbread" class="nav-link" v-on:click="closeMenu"
            >關於BBS</router-link
          >
        </li>
        <li class="nav-item">
          <router-link to="/news" class="nav-link" v-on:click="closeMenu"
            >最新消息</router-link
          >
        </li>
        <li class="nav-item">
          <router-link to="/menu" class="nav-link" v-on:click="closeMenu"
            >菜單</router-link
          >
        </li>
        <li class="nav-item">
          <a class="nav-link" v-on:click="openSidebar">
            購物車 <span class="cart-badge">{{ cartItemCount }}</span>
          </a>
        </li>
      </ul>

      <!-- 遮罩 (mask) -->
      <div
        class="menu-mask"
        v-bind:class="{ active: isMenuOpen }"
        v-on:click="closeMenu"
      ></div>
    </div>
  </nav>
</template>

<script setup>
import { ref, defineEmits, computed } from "vue";
import { useStore } from "vuex";

const store = useStore();
// 使用 computed 來計算 cart 中商品的總數量
const cartItemCount = computed(() => store.getters.cartQuantity);

const isMenuOpen = ref(false);
// 切換菜單開關
const toggleMenu = () => {
  isMenuOpen.value = !isMenuOpen.value;
};
// 關閉菜單
const closeMenu = () => {
  isMenuOpen.value = false;
};

// 定義發出事件的方法
const emit = defineEmits(["OpenSidebar"]);
// 顯示側欄
const openSidebar = () => {
  // 觸發事件通知父組件顯示側欄
  emit("OpenSidebar");
};
</script>
