import { createStore } from 'vuex';

const store = createStore({
  state: {
    cart: [],
    cartMessage: false,
    toastMessage: '',
  },
  mutations: {
    setCart(state, cart) {
      state.cart = cart;
    },
    addToCart(state, item) {
      const existingItem = state.cart.find(cartItem => cartItem.id === item.id);
      if (existingItem) {
        existingItem.quantity += 1;
      } else {
        state.cart.push({ ...item, quantity: 1 });
      }
      localStorage.setItem('cart', JSON.stringify(state.cart)); // 同步到 localStorage
      state.cartMessage = true; // 顯示提示視窗
      state.toastMessage = `${item.name} 已成功加入購物車！`; // 設定提示訊息

      // 2秒後清除提示訊息
      setTimeout(() => {
        state.toastMessage = ''; // 清除提示訊息
      }, 2000);
    },
    updateCart(state, updatedCart) {
      state.cart = updatedCart;
    },
    showCartMessage(state) {
      state.cartMessage = true;
    },
    hideCartMessage(state) {
      state.cartMessage = false;
    }
  },
  getters: {
    cartQuantity(state) {
      return state.cart.reduce((sum, item) => sum + item.quantity, 0);
    },
    totalPrice(state) {
      return state.cart.reduce((total, item) => total + (item.price * item.quantity), 0);
    },
    toastMessage(state) {
      return state.toastMessage; // 返回提示訊息
    }
  },
});

export default store;
