<template>
  <section class="brand-story">
    <div>
      <!-- 主要區塊 -->
      <div class="content">
        <!-- 左邊圖片 -->
        <div class="image-block left">
          <img
            src="img/animated/left-bread.png"
            alt="左側圖片"
            ref="imageRefs"
          />
        </div>
        <!-- 文字區塊 -->
        <div class="text-block" data-aos="zoom-in">
          <h2>BBS 品牌故事</h2>
          <p>
            在 Bread Baking
            Studio，每一顆麵包都是我們對烘焙的熱愛與堅持。從揉捏麵糰的第一刻，到麵包在烤箱裡散發出溫暖香氣，每個步驟都承載著我們對手作的尊重與用心。我們相信，好的麵包不只是填飽肚子，更是一種生活的美好體驗。因此，我們選用最純粹的食材，堅持天然發酵，讓每一口都充滿濃厚的麥香與層次。
          </p>
          <img
            src="img/animated/bread-1.png"
            alt="裝飾麵包1"
            class="deco-bread1"
          />
          <img
            src="img/animated/bread-4.png"
            alt="裝飾麵包2"
            class="deco-bread2"
          />
        </div>
        <!-- 右邊圖片 -->
        <div class="image-block right">
          <img
            src="img/animated/right-bread.png"
            alt="右側圖片"
            ref="imageRefs"
          />
        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import { onMounted, onUnmounted } from "vue";

let observer;

onMounted(() => {
  const blocks = document.querySelectorAll(".image-block"); // 確保已渲染的 DOM
  observer = new IntersectionObserver(
    (entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.classList.add("active");
        } else {
          entry.target.classList.remove("active"); // 支援重複觸發
        }
      });
    },
    {
      threshold: 0.1,
    }
  );

  blocks.forEach((block) => observer.observe(block));
});

onUnmounted(() => {
  if (observer) observer.disconnect();
});
</script>
